@use "../settings";
@use "../settings-options";
@use "../mixins/breakpoints";
@use "../mixins/float";

@if settings-options.$enable-utility-float {
  @each $breakpoint in settings.$utility-float-breakpoints {
    $bprule: breakpoints.breakpoint-designator($breakpoint);

    @include breakpoints.media-breakpoint-up($breakpoint) {
      .float#{$bprule}-start {
        @include float.float-start();
      }
      .float#{$bprule}-end {
        @include float.float-end();
      }
      .float#{$bprule}-none {
        @include float.float-none();
      }
    }
  }
}
